<template>
  <v-timeline class="blog-timeline">
    <v-timeline-item
      v-for="(data, i) in timeLineData"
      :key="i" small
      :color="data.color">
      <template v-slot:icon>
        <v-avatar :color="data.color" size="20">
          <span class="year-text">{{ data.year | cookYear }}</span>
        </v-avatar>
      </template>
      <template v-slot:opposite>
        <i :style="{color:data.color}" class="timeline-date-text"> {{ data.date }}</i>
      </template>

       <v-card dark max-width="400" @click="blogClickEvent(data)">
        <v-toolbar flat :color="data.color" dark tile>
          <v-card-subtitle>{{ data.title | cookTitle }}</v-card-subtitle> 
        </v-toolbar>
        <v-card-text class="white" style="height:50px" dark>
          <v-btn icon v-for="item in data.tags" class="blog-timeline-tag"><i :style="{color:data.color}">{{ item }}</i></v-btn>
        </v-card-text>
      </v-card>

    </v-timeline-item>
  </v-timeline>
</template>

<script>
  import { mapGetters } from "vuex";
  export default {
    components: {

    },
    data(){
      return {

      }
    },
    methods: {
      blogClickEvent(item){
        console.log(item);
      }
    },
    filters: {
      cookYear(value){
        value = value + ""
        if(value){
          try {
            return value - 2000;
          } catch (error) {
            try {
              return value.substring(2,4);
            } catch (error) {
              return value;
            }
          }
        }
        return value
      }, 
      cookTag(value) {
        return 'fa-home'
      },
      cookTitle(value) {
        if(value && value.length > 32){
          return value.substring(0, 33) + "..."
        }
        return value
      }
    },
    computed: {
      timeLineData(){
        return this.$store.state.timeLinePath;
      }
    },
  }
</script>

<style lang="stylus" scoped>
.blog-timeline
  top  0
  left 0
  width 100%
  height  100%
  overflow  auto
  position relative
  background-size cover
  background-image url('../assets/imgs/bg/13.jpg')

.timeline-date-text
  font-size 12px

.year-text
  font-size 15px
  color #ffffff

.blog-timeline-tag
  margin-right  5px
  font-size 10px
</style>